export default {
  urlToHttps: function(url) {
    let result = url.replace('https://', '//').replace('http://', '//').replace(':80/', '/').replace(':443/', '/').replace('//', 'https://');

    if (result.substr(0, 8) !== 'https://') {
      result = 'https://' + result;
    }

    return result;
  },

  getOwnPropertyNames: function(obj, log = true) {
    try {
      return Object.getOwnPropertyNames(obj).filter(function(pro) {
        let result = true;
        // result = typeof window[p] === 'function';
        // result = typeof window[p] === 'object';
        // result = typeof window[p] === 'string';
        if (result && log) {
          window.console.log(obj.constructor.name + ' have ' + (typeof obj[pro]) + ': ', pro);
        }
        return result;
      }).toString();
    } catch(e) {
      return '';
    }
  }
};
